/* App.css */

.wide-element {
    width: 100%;
    margin-bottom: 20px;
    padding: 10px;
    background-color: #f1f1f1;
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;

  }
  
  .wide-element-image {
    width: 100%;
    max-height: 300px;
    object-fit: contain;
    margin-bottom: 10px;
  }
  
.carousel-status {
    display: none;
  }
 .carousel.carousel-slider .control-dots {
  display: none;
}
  
  .wide-element-title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  .control-arrow {
  background-color: #ff0000;
  color: #ffffff;
  font-size: 24px;
  width: 40px;
  height: 40px;
}

.control-prev {
  left: 10px;
}

.control-next {
  right: 10px;
}
  .wide-element-description {
    font-size: 16px;
  }
  
  .element {
  
    width: 300px;
    margin-bottom: 20px;
    margin-left: 5px;
    margin-right: 5px;
    padding: 0 10px;
    background-color: #f1f1f1;
    text-align: center;
  }
  
  .element-image {
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 10px;
  }
  
  .element-title {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .element-description {
    font-size: 14px;
  }
  
  .container {
display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Center the elements horizontally */
  align-items: flex-start; /* Align the elements to the top */
  margin: 0 -10px; /* Add negative margin to counter the padding on the elements */
  }